import React, { useState } from 'react'

//Components
import Backdrop from '@mui/material/Backdrop'
import Box from '@mui/material/Box'
import Modal from '@mui/material/Modal'
import Fade from '@mui/material/Fade'
import Button from '@mui/material/Button'
import Typography from '@mui/material/Typography'
import { TextField } from '@mui/material'

//Validation
import * as yup from 'yup'
import { useFormik } from 'formik'

//Firebase
import database from '../../../firebase/Firestore'
import { ref, set } from 'firebase/database'

const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: 250,
    bgcolor: 'background.paper',
    border: '2px solid #000',
    boxShadow: 24,
    p: 4,
}

const validationSchema = yup.object({
    itemName: yup.string('Enter the item name').required('Item name is required'),
    itemQuantity: yup.string('Enter the item quantity').required('Item quantity is required'),
})

export default function AddToCart() {
    const [open, setOpen] = React.useState(false)
    const handleOpen = () => setOpen(true)
    const handleClose = () => setOpen(false)

    // const [itemName, setItemName] = useState('')
    // const [quantity, setQuantity] = useState(1)

    const formik = useFormik({
        initialValues: {
            itemName: '',
            itemQuantity: 1,
        },
        validationSchema: validationSchema,
        onSubmit: (values) => {
            // alert(values)
            // alert(JSON.stringify(values, null, 2))
            handleAddItem(values)
        },
    })

    const handleAddItem = async (values) => {
        const itemName = values.itemName;
        const quantity = values.itemQuantity;
        console.log('add item')
        console.log(itemName)
        console.log(quantity)

        // e.preventDefault()
        const data = {
            name: itemName,
            quantity: quantity,
            bought: false,
            forRecipe: '--',
        }

        set(ref(database, 'ShoppingCart/' + itemName), data)
        handleClose()
    }

    const handleItemNameOnChange = (event) => {
        // setItemName(event.target.value)
    }

    const handleQuantityOnChange = (event) => {
        // setQuantity(event.target.value)
    }

    return (
        <div>
            <Button onClick={handleOpen}>Add To Cart</Button>
            <Modal
                aria-labelledby="transition-modal-title"
                aria-describedby="transition-modal-description"
                open={open}
                onClose={handleClose}
                closeAfterTransition
                BackdropComponent={Backdrop}
                BackdropProps={{
                    timeout: 500,
                }}
            >
                <Fade in={open}>
                    <Box sx={style}>
                        <form onSubmit={formik.handleSubmit}>
                            <Typography
                                id="transition-modal-title"
                                variant="h6"
                                component="h2"
                            >
                                Add Item to the Shopping Cart
                            </Typography>
                            <TextField
                                label="Item Name"
                                name="itemName"
                                variant="standard"
                                // value={itemName}
                                required
                                autoFocus
                                // onChange={handleItemNameOnChange}
                                value={formik.values.itemName}
                                onChange={formik.handleChange}
                                error={formik.touched.itemName && Boolean(formik.errors.itemName)}
                                helperText={formik.touched.itemName && formik.errors.itemName}
                            />
                            <TextField
                                label="Quantity"
                                name="itemQuantity"
                                variant="standard"
                                
                                // value={quantity}
                                type="number"
                                // onChange={handleQuantityOnChange}
                                value={formik.values.itemQuantity}
                                onChange={formik.handleChange}
                                error={formik.touched.itemQuantity && Boolean(formik.errors.itemQuantity)}
                                helperText={formik.touched.itemQuantity && formik.errors.itemQuantity}
                            />
                            <Button  type="submit">Add</Button>
                            {/* <Typography
                            id="transition-modal-description"
                            sx={{ mt: 2 }}
                        >
                            Duis mollis, est non commodo luctus, nisi erat
                            porttitor ligula.
                        </Typography> */}
                        </form>
                    </Box>
                </Fade>
            </Modal>
        </div>
    )
}
